.wrapper {
  width: 664px;
  height: 906px;
  background: #ffffff;
  box-shadow: 0px 4px 33px rgba(0, 0, 0, 0.04);
  border-radius: 16px;
  padding: 48px 32px;
}
.arrow {
  margin: 0 20px;
}
.main__header {
  display: flex;
  height: 38px;
  align-items: flex-end;
  line-height: 37px;
  margin-bottom: 28px;
  justify-content: space-between;
}
.date {
  font-family: "Roboto";
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 28px;
  color: #41522e;
}
.main__searchResult {
  overflow: auto;
  height: 530px;
  margin-bottom: 48px;
}
.main__searchResult::-webkit-scrollbar {
  width: 2px;
  height: 100px;
  background-color: #e7e7e7;
}
.main__searchResult::-webkit-scrollbar-thumb {
  border-radius: 2px;
  background-color: #41522e;
}
.selected {
  margin-bottom: 39px;
}
.selectedCounter {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-size: 17px;
  line-height: 22px;
  letter-spacing: -0.408px;
  color: #41522e;
}
